* {
    margin: 0;
    padding: 0;
    border: 0;
    box-sizing: border-box;
    list-style: none;
   //color: #000000;
    text-decoration: none;
    background: none;
    outline: none;
}

table,
th,
td {
    border-collapse: initial;
    box-sizing: border-box;
    text-indent: initial;
    border-spacing: unset;
    border: none;
}

a {
    cursor: pointer;
}
